.hw-input {
  display: inline-block;
  width: 100%;
  min-height: 34px;
  font-size: 14px;
  padding: 0 10px;
  border: 1px solid #d6d6d6;
  transition-duration: .2s;
  box-sizing: border-box;
  &:hover {
    border-color: #c0c4cc;
  }
  &:focus {
    border-color: $primary;
    outline: none;
  }
  &::placeholder {
    color: #acacac;
  }
  &[disabled] {
    background: #f8f8f8;
    border-color: #d6d6d6;
    user-select: none;
  }
  &.sm {
    height: 30px;
    min-height: 30px;
  }
  &.lg {
    height: 40px;
  }
}
